.header {

	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 50;
	min-height: 190px;

	@include md {
		min-height: 100px;
	}

	&:before {
		content: "";
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;	
		background: rgb(0,0,0);
background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(255,255,255,0) 100%);
		z-index: -1;
		@include md {
			z-index: 2;
		}
	}

	@include md {
		&__burger {
			display: block;
			position: relative;
			width: 30px;
			height: 20px;
			z-index: 3;
			cursor: pointer;

			span {
				display: block;
				top: 9px;
				position: absolute;
				background-color: #fff;
				width: 100%;
				height: 2px;
				left: 0;
				transition: 0.3s;
			}

			&:before,&:after {
				display: block;
				content: "";
				position: absolute;
				background-color: #fff;
				width: 100%;
				height: 2px;
				left: 0;
				transition: 0.3s;
			}

			&.active span {
				transform: scale(0);
			}

			&.active:before {
				transform: rotate(45deg);
				top: 9px;
			}

			&.active:after {
				transform: rotate(-45deg);
				bottom: 9px
			}

			&:before{
				top: 0;
			}
			&:after{
				bottom: 0;
			}
		}
	}

	&__logo {
		font-family: 'Carter One', cursive;
		display: flex;
		align-items: center;
		color: #ffffff;
		font-size: 40px;
		text-transform: uppercase;
		align-self: flex-start;

		img {
			margin-right: 13px;
			@include lg {
				width: 30px;
				@include xs {
					width: 20px;
				}
			}
		}

		@include lg {
			font-size: 25px;
			
			@include md {
				position: relative;
				z-index: 3;

				@include xs {
					font-size: 20px;
				}
			}
		}
	}

	&__body {
		display: flex;
		flex-direction: column;

		@include md {
			flex-direction: row;
			align-items: center;
		}
	}

	&__inner {
		display: flex;
		justify-content: space-between;
		padding-top: 34px;
	}

	&__contact {
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		justify-content: space-between;
		@include xl {
			margin-bottom: 10px;
			@include lg {
				margin-bottom: 10px;
				justify-content: flex-end;
	
				@include md {
					position: relative;
					z-index: 3;
					margin-bottom: 0;
					margin-right: 20px;
					@include xs {
						margin-right: 10px;
					}
				}
			}
		}
	}

	&__mail {
		color: #ffffff;
		font-size: 16px;
		font-weight: 400;
		//margin-right: 35px;
		display: flex;
		align-items: center;
		@include lg {
			font-size: 14px;
			margin-right: 10px;
			@include md {
				display: none;
			}
		}
		

		i {
			margin-right: 8px;
			font-size: 17px;
			@include lg {
				font-size: 14px;
			}
		}
	}

	&__tell {
		color: #ffffff;
		font-size: 16px;
		font-weight: 400;
		//margin-right: 13px;
		display: flex;
		align-items: center;

		i {
			margin-right: 8px;
			font-size: 17px;

			@include lg {
				font-size: 14px;
			}
		}

		@include lg {
			font-size: 14px;
			margin-right: 10px;
			@include md {
				display: none;
			}
		}
	}

	&__time {
		font-weight: bold;
		color: #fff;
		font-size: 30px;
		background-color: #d5002d;
		padding: 6px 11px;

		@include lg {
			font-size: 25px;
			@include xs {
				font-size: 20px;
				padding: 4px 9px;
			}
		}
	}
}

.header-nav {
	display: flex;

	&__link {
		font-size: 13px;
		font-weight: 600;
		text-transform: uppercase;
		&:hover {
			background-color: #d5002d;
			@include md {
				background-color: transparent;
			}
		}
		a {
			color: #fff;
			padding: 16px 28px;
			display: block;

			@include lg {
				padding: 12px 20px;
				@include md {
					padding: 0;
					margin-bottom: 20px;
					font-size: 16px;
					display: inline-block;
				}
			}
		}

		@include md {
			display: block;
			margin-left: auto;
			margin-right: auto;
		}
	}

	&__mail,&__tell {
		display: none;
	}

	@include md {
		display: block;
		padding-top: 100px;
		position: fixed;
		width: 100%;
		height: 100%;
		left: 0;
		right: 0;
		background-color: rgba(0,0,0,0.9);
		transition: 1s;
		top: -100%;
		overflow: scroll;
		padding-left: 50px;
		&__mail,&__tell {
			display: block;
			font-size: 16px;
			margin-bottom: 16px;
		}

		.separator {
			display: block;
			width: 100px;
			height: 2px;
			opacity: 0.3;
			background-color: #fff;
			border: none;
			position: relative;
			margin-bottom: 20px;
		}

		&.active {
			top: 0;
			overflow: scroll;
			position: fixed;
		}
	}
}

body.lock{
	overflow: hidden !important;
}