.gallery {

	padding: 75px 0px 97px 0px;
	background-image: url("../img/gallery-bg.jpg");
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;

	@include xl {
		padding: 60px 0px;

		@include xs {
			padding: 40px 0px;
		}
	}

	&__photos {
		display: flex;
		flex-wrap: wrap;

		@include lg {
			justify-content: center;
		}
	}

	&__photo {
		flex: 1 1 25%;
		min-height: 250px;
		position: relative;
		overflow: hidden;
		cursor: pointer;

		@include xl {
			min-height: 200px;

			@include lg {
				flex: 1 1 33.333%;
				max-width: 33.333%;

				@include md {
					flex: 1 1 50%;
					max-width: 50%;
					
					@include xs {
						flex: 1 1 100%;
						max-width: 275px;
						margin-bottom: 10px;
					}
				}
			}
		}

		img {
			@include center();
			position: absolute;
		}
	}

	&__decor {
		position: absolute;
		background-color: rgba(#d5002d, 0.8);
		height: 100%;
		width: 100%;
		left: 0;
		z-index: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		top: -100%;
		border-bottom-left-radius: 100%;
		border-bottom-right-radius: 100%;
	}
}

.gallery__photo:hover .gallery__decor {
	transition: 0.3s;
	top: 0;
	border-radius: 0px;
}

