.blog {
	padding-top: 40px;
	padding-bottom: 75px;

	@include lg {
		padding-top: 20px;
		padding-bottom: 35px;
	}

	&__inner {
	}

	&__title {
		margin-bottom: 30px;

		@include lg {
			margin-bottom: 15px;
		}
	}

	&__subtitle {
		margin-bottom: 70px;
		width: 70%;
		margin-left: auto;
		margin-right: auto;
		display: block;

		@include lg {
			margin-bottom: 30px;

			@include md {
				width: 100%;

				@include xs{
					line-height: 1.5;
				}
			}
		}
	}

	&__newss {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
		justify-content: center;

		@include md {
			margin: 0;
		}
	}
}

* {
	// border: 1px solid green;
}
.blog-news {
	padding: 0px 15px;
	flex: 1 1 33.333%;
	max-width: 33.333%;
	position: relative;

	@include md {
		flex: 1 1 90%;
		max-width: 90%;
		margin-bottom: 40px;
		padding: 0;
		&:last-child {
			margin-bottom: 0;
		}

		@include xs {
			flex: 1 1 100%;
			max-width: 100%;
		}
	}

	&__img {
		height: 350px;
		background-position: center center;
		background-repeat: no-repeat;
		clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
		margin-bottom: 38px;

		@include xl {
			height: 280px;
			margin-bottom: 20px;

			@include lg {
				height: 180px;
				margin-bottom: 10px;

				@include md {
					height: 300px;
					margin-bottom: 20px;

					@include xs {
						height: 250px;
					}
				}
			}
		}
	}

	&__date {
		width: 94px;
		height: 67px;
		background-color: #d5002d;
		color: #ffffff;
		font-size: 16px;
		font-weight: 600;
		line-height: 24px;
		text-transform: uppercase;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		position: absolute;
		z-index: 1;
		right: 15px;
		top: 274px;

		@include xl {
			top: 204px;
			@include lg {
				width: 65px;
				height: 50px;
				font-size: 14px;
				line-height: 1.2;
				top: 124px;

				@include md {
					right: 0;
					width: 94px;
					height: 67px;
					font-size: 16px;
					top: 220px;

					@include xs {
						width: 65px;
						height: 50px;
						font-size: 14px;
						top: 180px;
					}
				}
			}
		}
	}

	&__title {
		color: #000000;
		font-family: 'Poppins', sans-serif;
		font-size: 16px;
		font-weight: 600;
		line-height: 24px;
		text-transform: uppercase;

		@include lg {
			font-size: 15px;
			line-height: 1.2;

			@include md {
				font-size: 20px;
			}
		}
	}
}
