.container{
	margin: auto;
	width: 1110px;
}

.button {
	color: #ffffff;
	font-size: 14px;
	font-weight: 600;
	line-height: 24px;
	text-transform: uppercase;
	padding: 15px 35px;
	background-color: #d5002d;
	display: inline-block;
}

.title {
	color: #d5002d;
	font-size: 30px;
	font-weight: 600;
	line-height: 24px;
	text-transform: uppercase;
	span.dark {
		color: #000000;
	}
	span.light {
		color: #ffffff;
	}
	&_c {
		text-align: center;
	}

	@include xs {
		line-height: 1.1;
		font-size: 25px;
	}
}

.subtitle {
	color: #000000;
	font-size: 14px;
	font-weight: 400;
	line-height: 24px;
	&_c {
		text-align: center;
	}

	&_light {
		color: #fff;
	}
}

video {
	margin-bottom: -4px;
}

