.about {
	padding-top: 32px;
	padding-bottom: 48px;


	&__inner {
		display: flex;
		position: relative;
		padding-top: 30px;

		&:before {
			display: block;
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 505px;
			height: 340px;
			background-image: url("../img/about-img.jpg");
			background-position: center center;
			background-size: cover;
			background-repeat: no-repeat;
			clip-path: polygon(92% 0, 100% 50%, 100% 75%, 100% 100%, 0 100%, 0 0);

			@include xs {
				width: 100%;
			}
		}
	}

	&__body {
		flex: 0 1 60%;
		background-color: #ebebeb;
		margin-left: auto;
		position: relative;
		z-index: 1;

		@include lg {
			flex-basis: 80%;
			@include md {
				flex-basis: 90%;
				@include xs {
					flex-basis: 98%;
				}
			}
		}
	}

	&__title {
		color: #d5002d;
		font-size: 30px;
		font-weight: 700;
		line-height: 24px;
		text-transform: uppercase;
		margin-bottom: 30px;
		span {
			color: #000000;
		}

		@include md {
			margin-bottom: 20px;
			@include xs {
				margin-bottom: 10px;
				font-size: 20px;
			}
		}
	}

	&__text {
		padding: 35px 30px 30px 30px;	
		//margin-bottom: 35px;
		@include xs {
			padding: 15px;
		}
	}

	&__descr {
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		p {
			margin-bottom: 25px;
			@include md {
				margin-bottom: 15px;
				@include xs {
					margin-bottom: 10px;
					font-size: 14px;
					line-height: 1.4;
				}
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&__button {
		@include xs {
			display: block;
			text-align: center;
		}
	}
}
