.form-block {
	padding-top: 100px;
	padding-bottom: 50px;

	@include lg {
		padding-top: 50px;

		@include md {
			padding-top: 35px;
			padding-bottom: 25px;
		}
	}

	&__inner {
		// display: flex;
		// flex: 1 1 50%;

		// & > div {
		// 	max-width: 50%;
		// }

		@include lg {
			// flex-wrap: wrap;
			// flex: 1 1 100%
		}
	}

	&__video-block {
		width: 55%;
		float: left;
		position: relative;
		clip-path: polygon(92% 0, 100% 50%, 100% 75%, 100% 100%, 0 100%, 0 0);

		@include lg {
			clip-path: none;
			width: 100%;
			display: block;
		}
	}

	&__text {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
		width: 100%;
		padding-top: 55px;
		padding-right: 148px;
		padding-left: 66px;

		@include xl {
			padding-top: 30px;
			padding-left: 30px;
			padding-right: 130px;
			@include md {
				display: none;
			}
		}

		&_mob {
			display: none;
			text-align: center;
			@include md {
				display: block;
			}
		}
	}

	&__title {
		margin-bottom: 35px;
		@include xl {
			margin-bottom: 10px;
		}

		&_mob {
			margin-bottom: 10px;
		}
	}

	&__subtitle {
		&_mob {
			margin-bottom: 30px;
		}
	}

	// * {
	// 	border: 1px solid green;
	// }

	&__video {
		// width: 604px;
		max-width: 100%;
		position: relative;

		video {
			max-width: 100%;
			// min-height: 413px;

			@include xl {
				@include lg {
					min-height: auto;
				}
			}

			// &-block{
			// 	// @include xl {
			// 	// 	max-width: 50%;
			// 	// }
			// }
		}
	}

	&__form {
		margin-left: -10%;
		position: relative;
		z-index: 10;
		background-color: #ebebeb;
		margin-top: 36px;
		width: 55%;
		float: left;
		// flex-basis: 70%;

		@include lg {
			margin-left: 0;
			width: 100%;
			display: block;
			// flex-basis: 100%;
		}
	}
}

.form-preload {
	img {
		max-width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}

	img.play-button{
		position: absolute;
		@include center();
		cursor: pointer;
		z-index: 5;
		margin-top: 40px;

		@include md {
			margin-top: 0;
		}
	}
	&:before {
		display: block;
		content: "";
		background-color: #000;
		opacity: 0.7;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		position: absolute;
	}
	
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-image: url("../img/video-preloader.jpg");
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	&.disabled {
		display: none;
	}
}
.quote-form {
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	&__title {
		margin-bottom: 32px;
		@include xs {
			text-align: center;
			font-size: 25px;
			margin-bottom: 10px;
		}
	}

	&__row {
		margin-bottom: 14px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__body {
		padding: 40px 32px 30px 32px;

		@include xs {
			padding: 20px;
		}
	}

	&__input {
		padding: 15px;
		width: 100%;
		border: 1px solid #cccccc;
		background-color: transparent;

		@include xs{
			font-size: 14px;
			padding: 10px;
		}
	}

	&__text-area {
		padding: 15px;
		width: 100%;
		resize: none;
		height: 86px;
		border: 1px solid #cccccc;
		background-color: transparent;
		@include xs{
			padding: 10px;
			font-size: 14px;
		}
	}

	&__button {
		border: none;
	}

	&__button-block {
		@include xs {
			text-align: center;
			margin-bottom: 10px;
		}
	}
}

