.services {
	padding-top: 48px;
	padding-bottom: 60px;
	@include xs {
		padding-top: 24px;
		padding-bottom: 30px;
	}

	&__title {
		margin-bottom: 30px;
		@include lg {
			margin-bottom: 20px;
		}
	}

	&__subtitle {
		margin-bottom: 60px;
		display: block;
		margin-left: auto;
		margin-right: auto;
		width: 70%;
		@include lg {
			margin-bottom: 40px;
			width: 100%;
			@include xs {
				margin-bottom: 20px;
			}
		}
	}

	&__blocks {
		display: flex;
		flex-wrap: wrap;
	}
}
.service-block {
	flex: 1 1 33.3333%;
	max-width: 33.333%;
	padding: 22px 28px 28px 22px;
	transition: 0.3s;

	@include lg {
		flex: 1 1 50%;
		max-width: 50%;

		@include xs {
			flex-basis: 100%;
			max-width: 100%;
		}
	}
	position: relative;

	&:nth-child(1) {
		border-right: 1px solid rgba(0, 0, 0, 0.2);
		border-bottom: 1px solid rgba(0, 0, 0, 0.2);

		@include xs {
			border-right: 0px;
			border-top: 0px;
			border-left: 0px;
			border-bottom: 1px solid rgba(0, 0, 0, 0.2);
		}
	}

	&:nth-child(2) {
		border-right: 1px solid rgba(0, 0, 0, 0.2);
		border-bottom: 1px solid rgba(0, 0, 0, 0.2);

		@include lg {
			border-right: 0px;

			@include xs {
				border-right: 0px;
				border-top: 0px;
				border-left: 0px;
				border-bottom: 1px solid rgba(0, 0, 0, 0.2);
			}
		}
	}

	&:nth-child(3) {
		border-bottom: 1px solid rgba(0, 0, 0, 0.2);

		@include lg {
			border-right: 1px solid rgba(0, 0, 0, 0.2);

			@include xs {
				border-right: 0px;
				border-top: 0px;
				border-left: 0px;
				border-bottom: 1px solid rgba(0, 0, 0, 0.2);
			}
		}
	}

	&:nth-child(4) {
		border-right: 1px solid rgba(0, 0, 0, 0.2);

		@include lg {
			border-right: 0;
			border-bottom: 1px solid rgba(0, 0, 0, 0.2);

			@include xs {
				border-right: 0px;
				border-top: 0px;
				border-left: 0px;
				border-bottom: 1px solid rgba(0, 0, 0, 0.2);
			}
		}
	}

	&:nth-child(5) {
		border-right: 1px solid rgba(0, 0, 0, 0.2);

		@include xs {
			border-right: 0px;
			border-top: 0px;
			border-left: 0px;
			border-bottom: 1px solid rgba(0, 0, 0, 0.2);
		}
	}

	&:nth-child(6) {
		@include xs {
			border-bottom: none;
		}
	}


	&:hover {
		background-color: #d5002d;
		cursor: pointer;
	}

	&:hover &__decor {
		display: block;
		background-color: #ffffff;
		opacity: 0.1;
		clip-path: polygon(0 0, 100% 0, 100% 100%, 36% 100%);
		width: 50%;
		height: 100%;
		position: absolute;
		right: 0;
		top: 0;
	}

	&:hover .service-block__link {
		color: #ffffff;
		transition: 0.3s;
	}

	&:hover .service-block__title {
		color: #ffffff;
		transition: 0.3s;
	}

	&:hover .service-block__descr {
		color: #ffffff;
		transition: 0.3s;
	}

	&:hover .service-block__img svg {
		fill: white;
		transition: 0.3s;
	}

	&__img {
		svg {
			height: 60px;
			display: inline-block;
			text-align: left;
			margin-right: 255px;
			@include xl {
				margin-right: 200px;
				@include lg {
					margin-right: 240px;
					@include md {
						margin-right: 150px;
						@include xs {
							margin-right: 0;
						}
					}
				}
			}
		}
		margin-bottom: 15px;
		position: relative;
		z-index: 1;
		display: flex;
		@include xs {
			display: flex;
			justify-content: space-around;
		}
	}

	&__title {
		font-size: 20px;
		font-weight: 600;
		text-transform: uppercase;
		margin-bottom: 25px;
		position: relative;
		z-index: 1;
		@include md {
			margin-bottom: 15px;
			@include xs {
				text-align: center;
			}
		}
	}

	&__descr {
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		margin-bottom: 25px;
		position: relative;
		z-index: 1;
		@include md {
			margin-bottom: 15px;
			@include xs {
				text-align: center;
			}
		}
	}

	&__link {
		display: flex;
		color: #d5002d;
		align-items: center;
		text-transform: uppercase;
		font-size: 14px;
		font-weight: 600;
		position: relative;
		z-index: 1;
		i {
			margin-left: 6px;
			transition: 0s;
			@include xs {
				display: none;
			}
		}
		@include xs {
			justify-content: space-around;
		}
	}
}
