.top-box {
	padding-bottom: 32px;

	&__inner {
		margin-top: -65px;
		position: relative;

		@media screen and (max-width: 1300px) {
			margin-top: -20px;
		}

		@include xs {
			margin-top: -50px;
		}
	}
}
.top-box-slider {

	&__slider {
		background-image: linear-gradient(to right, #7b001a 0%, #d5002d 100%);
	}

	position: relative;

	&__control {
		display: flex;
		color: #ffffff;
		position: absolute;
		right: -75px;
		top: 5px;

		@media screen and (max-width: 1300px) {
			top: -40px;
			right: 20px;
		}

		@include xs {
			display: none;
		}
	}

	&__prev, &__next {
		font-size: 30px;
		transition: 0s;
		cursor: pointer;
		&:hover {
			color: #D5002D;
		}
	}

	&__prev {
		margin-right: 10px;
	}

}
.feature-item {
	color: #ffffff;
	padding: 28px 32px;
	height: 100%;
	position: relative;

	&__title {
		font-size: 24px;
		font-weight: 600;
		text-transform: uppercase;
		margin-bottom: 30px;
	}

	&__subtitle {
		font-size: 13px;
		font-weight: 400;
		line-height: 25px;
		margin-bottom: 20px;
	}

	&__link {
		display: flex;
		color: #ffffff;
		align-items: center;
		i {
			margin-left: 6px;
		}
	}

	&__img {
		position: absolute;
		right: 0;
		height: 140px;
		top: 50%;
		transform: translateY(-50%);
	}
}
