.footer {

	background-color: #000;

	&__inner {
		display: flex;
		flex-wrap: wrap;
		color: #fff;
		padding-top: 45px;
		padding-bottom: 45px;
	}

	&__column {
		flex: 1 1 25%;
		max-width: 25%;

		@include lg {
			flex: 1 1 33.3333%;
			max-width: 33.3333%;
			&:first-child {
				flex: 1 1 100%;
				max-width: 100%;
				margin-bottom: 30px;
			}

			@include xs {
				flex: 1 1 100%;
				max-width: 100%;
				text-align: center;
				margin-bottom: 30px;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	&__main {
	}

	&__logo {
		font-family: "Carter One";
		font-size: 36px;
		font-weight: 400;
		text-transform: uppercase;
		color: #fff;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		margin-bottom: 5px;

		img{
			margin-bottom: 3px;
		}
	}

	&__socials {
		display: flex;
		justify-content: center;
	}

	&__social {
		width: 40px;
		height: 40px;
		background-color: #d5002d;
		margin-right: 8px;
		border-radius: 50%;
		line-height: 40px;
		font-size: 20px;
		text-align: center;
		transition: 0.3s;
		color: #ffffff;
		&:last-child {
			margin-right: 0;
		}

		&:hover {
			color: #d5002d;
			background-color: #fff;
		}
	}

	&__links {
	}

	&__title {
		font-size: 20px;
		font-weight: 600;
		line-height: 24px;
		margin-bottom: 30px;

		@include xs {
			margin-bottom: 10px;
		}
	}

	&__list {
	}

	&__li {
		font-size: 14px;
		font-weight: 400;
		line-height: 30px;
		a {
			color: #ffffff;
		}
	}

	&__services {
	}

	&__info {
		.icon {
			margin-right: 15px;
			i {
				font-size: 14px;
			}	
		}
		li {
			display: flex;
			@include xs {
				justify-content: center;
			}
		}
	}

	&__address {
		font-size: 13px;
		margin-bottom: 25px;
		line-height: 24px;
		@include xs {
			margin-bottom: 0;
		}
	}

	&__tel {
		font-size: 13px;
		line-height: 24px;
	}

	&__email {
		font-size: 13px;
		line-height: 24px;
	}

	&__bottom-line {
		color: #ffffff;
		font-size: 13px;
		font-weight: 400;
		text-align: center;
		padding-top: 24px;
		padding-bottom: 24px;
		background-color: #111111;

		@include xs {
			font-size: 12px;
			padding-top: 12px;
			padding-bottom: 12px;
			padding-right: 12px;
			padding-left: 12px;
		}
	}
}
