.testimonial-block {
	padding-top: 50px;
	padding-bottom: 40px;

	@include md {
		padding-top: 25px;
		padding-bottom: 40px;
	}

	&__inner {
		padding-right: 20px;

		@include lg {
			padding-right: 0;
		}
	}

	&__title {
		margin-bottom: 6px;
		margin-left: 30px;

		@include lg {
			margin-left: 0;

			@include xs {
				text-align: center;
				margin-bottom: 10px;
			}
		}
	}

	&__body {
		position: relative;
		padding-right: 45px;

		@include lg {
			padding-right: 25px;

			@include xs {
				padding-right: 0;
			}
		}
	}

	&__slider-dots {
		position: absolute;
		right: 0;
		bottom: 0;
		li {

			&:last-child button{
				margin-bottom: 0;
			}

			button {
				width: 15px;
				height: 15px;
				display: block;
				border: none;
				position: relative;
				overflow: hidden;
				margin-bottom: 11px;

				@include xs {
					margin-bottom: 0px;
					margin-right: 10px;
				}

				&:before {
					content: "";
					display: block;
					width: 15px;
					height: 15px;
					top: 0;
					left: 0;
					background-color: #000;
					transition: 0.3s;
					cursor: pointer;
				}
			}

			&.slick-active {
				button:before {
					transition: 0.3s;
					border: 3px solid #d5002d;
					background-color: #fff;
				}
			}
		}

		@include xs {
			position: static;
			ul {
				display: flex;
				justify-content: center;
			}
		}

	}
}
.response {

	display: flex;
	padding-left: 30px;

	@include lg {
		padding-left: 15px;
		padding-top: 20px;
		
		@include md {
			flex-wrap: wrap;

			@include xs {
				padding-top: 0;
			}
		}
	}

	&__top-text {
		margin-top: 19px;
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		margin-bottom: 25px;
		display: block;
		width: 60%;

		@include lg {
			width: 100%;
			margin-bottom: 10px;
			margin-top: 0;

			@include md {
				margin-bottom: 10px;
			}
		}
	}

	&__text {
		margin-left: 65px;
		padding-left: 14px;
		border-left: 2px solid #000;
		position: relative;
		margin-bottom: 35px;
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;

		@include lg {
			line-height: 1.4;
			margin-bottom: 15px;
		}

		&:before {
			content: "";
			display: block;
			background-image: url("../img/response-dec.png");
			background-repeat: no-repeat;
			background-position: left left;
			width: 54px;
			min-height: 60px;
			position: absolute;
			left: -54px;
		}
	}

	&__author {
		display: flex;
		flex-direction: column;
	}

	&__author-name {
		flex: 1 1 auto;
		text-align: right;
		font-size: 14px;
		font-weight: 700;
		line-height: 20px;
	}

	&__author-status {
		text-align: right;
		// text-align: right;
		color: #595959;
		font-size: 13px;
		font-weight: 400;
		line-height: 20px;
	}

	&__column-body {
		padding-right: 24px;

		@include lg {
			flex: 1 1 70%;
			padding-right: 10px;

			@include md {
				flex: 1 1 100%;
			}
		}
	}

	&__column-img {

		@include lg {
			flex: 1 1 30%;
			display: flex;

			@include md {
				flex: 1 1 100%;
				display: none;
			}
		}

		img {
			flex: 1 1 100%;
			max-width: 100%;
			margin-top: auto;
		}
	}
}
