.banner {
	padding-top: 230px;
	padding-bottom: 110px;
	background-image: url("../img/banner-bg.jpg");
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;

	@include xl {
		padding-top: 200px;
		@include lg {
			padding-bottom: 90px;
				@include md {
				padding-top: 120px;
			}
		}
	}

	&__inner {
		display: flex;
		align-items: center;
		margin: 0 -30px;

		@include xl {
			margin: 0 -15px;

			@include lg {
				flex-wrap: wrap;
			}
		}
		
	}

	&__column {
		flex: 1 1 50%;
		padding: 0 30px;
		//max-width: 50%;

		@include xl {
			padding: 0 15px;

			@include lg {
				flex: 1 1 100%;
				//max-width: 100%;


				&:first-child {
					margin-bottom: 50px;
				}
			}
		}
	}

	&__body {
		@include lg {
			text-align: center;
		}
	}

	&__title {
		color: #ffffff;
		font-size: 38px;
		font-weight: 600;
		text-transform: uppercase;
		margin-bottom: 34px;
		@include xl {
			font-size: 34px;
			margin-bottom: 30px;
			@include lg {
				margin-bottom: 10px;

				@include xs {
					font-size: 25px;
				}
			}
		}
		
	}

	&__subtitle {
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		margin-bottom: 49px;

		@include lg {
			margin-bottom: 40px;

			@include xs {
				font-size: 14px;
				line-height: 1.4;
			}
		}
	}

	&__tell {
		background-color: #D5002D;
		padding: 13px;
		display: inline-block;
			
		.info {
			color: #ffffff;
			font-size: 13px;
			font-weight: 400;
			margin-bottom: 10px;
			display: block;
		}

		.tell {
			color: #ffffff;
			font-size: 35px;
			font-weight: 600;
			display: block;

			@include xs {
				font-size: 25px;
			}
		}
	}

	&__video {
		max-width: 100%;
		position: relative;

		video {
			max-width: 100%;
		}
	}

	&-preload {
		position: absolute;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0, 0.5);
		top: 0;
		left: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;

		&:before {
			display: block;
			content: "";
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			position: absolute;
			background: rgba(0,0,0, 0.3);
		}

		&.disabled {
			display: none;
		}

		img {
			cursor: pointer;
		}

		.play-button {
			position: relative;
			z-index: 5;
		}
	}
}
