.subscription-block {
	position: relative;

	&__inner {
		background-image: linear-gradient(to right, #7b001a 0%, #d5002d 100%);
		padding: 38px 30px;

		@include lg {
			padding: 20px 15px;
		}
	}
	&:before{
		display: block;
		content: "";
		position: absolute;
		height: 50%;
		background-color: #000;
		width: 100%;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
	}
}

.subscription-form {
	display: flex;
	align-items: center;

	@include xs {
		display: block;
	}

	&__text {
		margin-right: 15px;
		color: #ffffff;
		font-size: 40px;
		font-weight: 700;
		line-height: 24px;

		@include lg {
			font-size: 30px;

			@include md {
				font-size: 22px;
				margin-right: 10px;
				@include xs {
					margin-bottom: 30px;
					text-align: center;
				}
			}
		}
	}

	&__input {
		flex: 1 1 55%;
		margin-right: 15px;
		margin-left: 15px;
		padding: 15px;
		border: none;
		font-size: 14px;
		line-height: 24px;

		@include lg {
			padding: 10px;
			@include md {
				margin-left: 10px;
				padding: 6px;

				@include xs {
					margin-bottom: 10px;
					height: 40px;
					width: 80%;
					display: block;
					margin-left: auto;
					margin-right: auto;
				}
			}
		}
		
	}

	&__button {
		flex: 1 1 auto;
		background-color: #ffffff;
		border: none;
		padding: 19px 55px;
		color: #c4002a;
		font-size: 14px;
		font-weight: 600;
		text-transform: uppercase;
		@include lg {
			padding: 15px;

			@include md {
				padding: 10px;
				@include xs {
					display: block;
					margin-left: auto;
					margin-right: auto;
				}
			}
		}
	}
}
